import React, {Component} from 'react';
import { translate } from "react-translate";



class InfoWidget extends Component {
    render(){
        let {t } = this.props;

        return(
            <div className="col-lg-12 col-md-12 mt-3">
                <div className="f_widget company_widget text-center fadeInLeft" data--delay="0.2s">
                    <a href="/" className="f-logo"><img src={require("../../img/logo-blue.png").default} alt=""/></a>
                    <div className="widget-wrap f_p f_600">
                        <p className="f_size_20 mb-0 l_height34"><span>{t(this.props.slogan)}</span></p>
                    </div>
                    <div className="f_social_icon">
                        <a href="https://www.facebook.com/dreamcodesoft"><i className="ti-facebook"></i></a>
                        <a href="https://www.linkedin.com/company/dreamcode-software/"><i className="ti-linkedin"></i></a>
                        <a href="https://www.instagram.com/dreamcode_software/"><i className="ti-instagram"></i></a>
                        <a href="https://www.youtube.com/channel/UC2cXutvW1QF4q9NhFHiUZPg"><i className="ti-youtube"></i></a>
                    </div>
                </div>
            </div>
        )
    }
}

export default translate('info_widget')(InfoWidget);