import React, { Component } from 'react';
import Reveal from 'react-reveal/Reveal';
import { translate } from "react-translate";



const widgetEn = require('../../assets/translations/en.json');
const widgetEs = require('../../assets/translations/es.json');

let widget = null;

class AboutWidget extends Component {

    render() {

        let {  t } = this.props;

        const ActualLang = localStorage.getItem("lenguaje") ? localStorage.getItem("lenguaje") : 'en';

        if (ActualLang === 'en') {
            widget = widgetEn
        } else {
            widget = widgetEs
        }

        return (
            <Reveal effect="fadeInUp" className="col-lg-12 col-md-12" duration={1200}>
                <div className="col-lg-12">
                    <div className="f_widget about-widget d-flex justify-content-center fadeInLeft mb-4" data--delay="0.4s">
                        <div className="d-flex menuFooter justify-content-center ">
                            {
                                widget.aboutWidget.map((item, i) => {
                                    return (
                                        <div className='col-md-4 p-2 item' key={i}>
                                            <a href={item.url} className='p_color f_600 f_size_18'>{item.text}</a>                                    
                                        </div>
                                    )
                                })
                            }
                        </div>
                    </div>
                </div>
            </Reveal>
        )
    }
}

export default translate('footer')(AboutWidget);